const createCard = (preview, image, description) => {
return `<li class="gallery-item">
    <a class="gallery-link" href="${image}" data-lightbox="gallery" data-title="${description}">
    <img
        class="gallery-image"
        src="${preview}"
        alt="${description}"
    />
    </a>
</li>`;
};

const insertItems = (container, arr, item) => {
arr.forEach((el) => {
    container.insertAdjacentHTML("beforeend", item(el.preview, el.original, el.description));
});
};

const initGallery = () => {
const gallery = document.querySelector(".gallery");

insertItems(gallery, images, createCard);


const lightbox = new SimpleLightbox(".gallery a", {
    captionsData: "alt",
    captionDelay: 250,
});
};

initGallery();

